import createStyleContext from '@Tools/createStyleContext';
import type { StyleKeys, StylePool } from './types';

export const STYLE_KEYS = ['light', 'dark'] as const;

export const StyleContext = createStyleContext<StyleKeys>();

class Style {
  public stylePool: StylePool = [];

  private init(): void {
    this.stylePool = STYLE_KEYS.map((key) => ({
      key,
      style: require(`@Styles/${key}`).default,
    }));
  }

  constructor() {
    this.init();
  }

  public getStyle(key: StyleKeys): string {
    const find = this.stylePool.find((t) => t.key === key);
    if (!find) return ''; // fallback to empty style
    return find.style;
  }
}

export default new Style();
